@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

$body:#000;
$font:#fff;

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif; 
    scroll-behavior: smooth;
}
body{
    background: $body;
    overflow-x: hidden;
    width: 100%;

    &::-webkit-scrollbar{
        appearance: none;
        width: 8px;
    }
    &::-webkit-scrollbar-track{
       background: #000;
    }
    &::-webkit-scrollbar-thumb{
        background:crimson;
        border-radius: 2px;
     }
}
.header{
        width: 100%;
        height: 75px;
        display: flex;
        align-items: center; 
        background: $body;
        position: sticky;
        top: 0;
        z-index: 111;
        overflow: hidden;

    > .div_1,
    .menu_list,
    > .div_2{
        width: 50%;
        display: flex;
        justify-content:center;
        align-items: center;
        flex-wrap: wrap;

        @media only screen and (max-width: 991px){
            &{
                width: 100%; 
            }
        }
    }
    > .div_1{
        justify-content: start;
    }
    .logo_box{
        width: 8vmax;
        margin-left: 2rem;
        img{
            object-fit: contain;
            width: 100%;
            }
    }
    .menu_list{
        justify-content: space-evenly;
        a{
            text-decoration: none;
            color: $font;
            cursor: pointer;
        }
        @media only screen and (max-width: 991px){
            &{
            display: none;
            }
        }
    }
    > .div_2{
            justify-content: end;
            div{
                color: white;
                display: flex;
                justify-content: center;
                padding: 0 20px;
                cursor:pointer;
                font-size: 1.5rem;
                padding-right: 2rem;
                input{
                    display: none;
                }
           &:hover {
                border: 1px solid white;
                margin-right: 5px;
                border-radius: 5px;
                padding: 5px 20px;

                input{
                    display: block;
                    border: none;
                    outline: none;
                    background: transparent;
                    padding-left: 10px;
                    font-size: 16px;
                    color: #fff;

                    &::placeholder{
                       color: #fff;
                    }
                }
                @media only screen and (max-width:661px) {
                    padding: 5px 1px;
                    font-size: 16px;
                }
            }
           }
    } 
}
